ul {
    list-style: none;
  }
  
  p {
    margin: 0.5rem 0;
  }
  
  .footer {
    max-width: 70%;
    margin: 1rem auto;
    overflow: auto;
  }
  
  .footer,
  .footer a {
    color: #999;
    font-size: 0.9rem;
  }
  
  .footer p {
    margin-bottom: 1.5rem;
  }
  
  .footer .footer-cols {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2rem;
  }
  
  .footer li {
    line-height: 1.9;
  }
  
  .footer .lang-select {
    margin-top: 2rem;
    color: #999;
    background-color: #000;
    background-image: none;
    border: 1px solid #333;
    padding: 1rem 1.2rem;
    border-radius: 5px;
  }


select {
    padding: 10px;
    font-size: 16px;
    border: 2px solid #333;
    border-radius: 5px;
    background-color: black; 
    color: #fff; 
    width: 150px; 
    position: relative;
  }

  select::after {
    content: '\25BE'; 
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    pointer-events: none;
    color: #fff; 
  }
  
 
  select option {
    padding: 10px;
  }
  
  select option:checked {
    background-color: #333; 
  }
  
  
  
  @media (max-width: 960px) {
    .footer .footer-cols {
      grid-template-columns: repeat(2, 1fr);
    }
  }